import { combineReducers } from "redux";
import loginReducer from "./scenes/Login/loginReducer";
import appReducer from "./app/appReducer";
import tablesReducer from "./scenes/Tables/tablesReducer";
import profileReducer from "./scenes/Profile/profileReducer";
import companiesReducer from "./scenes/Companies/companiesReducer";

export default combineReducers({
  appReducer,
  loginReducer,
  tablesReducer,
  profileReducer,
  companiesReducer
});
