import React, { useState, useEffect } from "react";
import GridLoader from "react-spinners/GridLoader";
import Typography from "@material-ui/core/Typography";
import TextField from "@material-ui/core/TextField";
import Select from "@material-ui/core/Select";
import FormControl from "@material-ui/core/FormControl";
import FormControlLabel from "@material-ui/core/FormControlLabel";
import Checkbox from "@material-ui/core/Checkbox";
import InputLabel from "@material-ui/core/InputLabel";
import MenuItem from "@material-ui/core/MenuItem";
import Button from "@material-ui/core/Button";
import { makeStyles } from "@material-ui/core/styles";
import CircularProgress from "@material-ui/core/CircularProgress";
import { red } from "@material-ui/core/colors";
import { useSelector, useDispatch } from "react-redux";
import { rootUrl } from "../../config";
import { useHistory } from "react-router-dom";

import { getCompanies } from "../Companies/companiesActions";

import axios from "axios";
import { Menu } from "@material-ui/core";

const useGetCompanies = () => {
  const { isDataLoaded } = useSelector((state) => state.companiesReducer);
  const dispatch = useDispatch();
  useEffect(() => {
    !isDataLoaded && dispatch(getCompanies());
  }, []);
};

export default function AddProject() {
  const classes = useStyles();
  useGetCompanies();
  const { isDataLoading, isDataLoaded, companies } = useSelector(
    (state) => state.companiesReducer
  );
  const history = useHistory();

  const [isLoading, setIsLoading] = useState(false);
  const [selectedCompany, setSelectedCompany] = useState(-1);
  const [name, setName] = useState("");
  const [isRecording, setIsRecording] = useState(false);
  const [selectedTrans, setSelectedTrans] = useState(-1);
  const [selectedLanguage, setSelectedLanguage] = useState(-1);

  const [isError, setIsError] = useState(false);
  const [goodResponse, setGoodResponse] = useState("");

  const dispatch = useDispatch();

  const clearFields = () => {
    setSelectedCompany(-1);
    setName("");
  };

  const onSubmit = async () => {
    setIsLoading(true);

    try {
      const response = await axios.post(
        `${rootUrl}/project`,
        {
          project_name: name,
          company_id: selectedCompany,
          is_recording: isRecording,
          transcription: selectedTrans,
          language: selectedLanguage,
        },
        {
          withCredentials: true,
        }
      );
      clearFields();
      dispatch(getCompanies());
      setGoodResponse(response.data);
    } catch (e) {
      setIsError(true);
    } finally {
      setIsLoading(false);
    }
  };

  if (isDataLoading) {
    return (
      <div
        style={{
          width: "100%",
          height: "80vh",
          display: "flex",
          justifyContent: "center",
          alignItems: "center",
        }}
      >
        <GridLoader loading={true} size={50} color={"rgb(65,83,175)"} />
      </div>
    );
  }

  if (!isDataLoaded) {
    return null;
  }

  return (
    <div style={{ padding: 10 }}>
      <Typography variant="h5"> </Typography>

      <div style={{ display: "flex", flexDirection: "column", marginTop: 5 }}>
        <FormControl style={{ width: 200 }} variant="filled">
          <InputLabel></InputLabel>
          <Select
            value={selectedCompany}
            onChange={(e) => setSelectedCompany(e.target.value)}
          >
            {companies.map((c) => (
              <MenuItem value={c.id}>{c.name}</MenuItem>
            ))}
          </Select>
        </FormControl>
        <TextField
          className={classes.textField}
          label=""
          variant="filled"
          value={name}
          onChange={(e) => setName(e.target.value)}
        />

        <FormControl style={{ width: 200, margin: "5px 0px" }} variant="filled">
          <InputLabel></InputLabel>
          <Select
            value={selectedTrans}
            onChange={(e) => setSelectedTrans(e.target.value)}
          >
            <MenuItem value="N"></MenuItem>
            <MenuItem value="Y">Yandex</MenuItem>
            <MenuItem value="G">Google</MenuItem>
            <MenuItem value="A">ASM</MenuItem>
          </Select>
        </FormControl>
        <FormControl style={{ width: 200, margin: "5px 0px" }} variant="filled">
          <InputLabel></InputLabel>
          <Select
            value={selectedLanguage}
            onChange={(e) => setSelectedLanguage(e.target.value)}
          >
            <MenuItem value="R"></MenuItem>
            <MenuItem value="E"></MenuItem>
          </Select>
        </FormControl>

        <FormControlLabel
          control={
            <Checkbox
              checked={isRecording}
              onChange={(e) => setIsRecording(!isRecording)}
              color="primary"
            />
          }
          label=" "
        />
      </div>

      {isError && <Typography variant="subheader"></Typography>}

      {goodResponse !== "" && (
        <Typography variant="h6" style={{ margin: "10px 0px" }}>
          {goodResponse}
        </Typography>
      )}

      <div className={classes.submitButtonWrapper}>
        <Button
          type="submit"
          fullWidth
          variant="contained"
          color="primary"
          className={classes.submit}
          disabled={
            isLoading ||
            name === "" ||
            selectedCompany === -1 ||
            selectedLanguage === -1 ||
            selectedTrans === -1
          }
          onClick={onSubmit}
        >
          {!isLoading ? " " : <span>&nbsp;&nbsp;</span>}
        </Button>
        {isLoading && (
          <CircularProgress
            size={24}
            className={classes.submitButtonProgress}
          />
        )}
      </div>
    </div>
  );
}

const useStyles = makeStyles((theme) => ({
  submitButtonWrapper: {
    margin: theme.spacing(1),
    position: "relative",
    width: 200,
  },
  submitButtonProgress: {
    position: "absolute",
    top: "50%",
    left: "50%",
    marginTop: -8,
    marginLeft: -12,
  },
  submit: {
    margin: theme.spacing(3, 0, 2),
  },
  textField: {
    width: 200,
    margin: "5px 0px",
  },
}));
