import { handleActions } from "redux-actions";
import tablesActions from "./tablesActions";
import { tableNameEnum } from "./tablesUtil";

const defaultState = {
  initData: {},
  searchField: "",
  filterList: [],
  currentTable: tableNameEnum.calls,
  projectId: "",
  loading: true,
};

export default handleActions(
  {
    [tablesActions.setLoading]: (state, action) => {
      return {
        ...state,
        loading: action.payload,
      };
    },

    [tablesActions.setInitData]: (state, action) => {
      const { tableName, response } = action.payload;
      return {
        ...state,
        loading: false,
        initData: {
          ...state.initData,
          [tableName]: response,
        },
      };
    },
  },
  defaultState
);
