import { createAction } from "redux-actions";
import axios from "axios";
import appActions from "../../app/appActions";

import { rootUrl } from "../../config";

const actions = {
  login: createAction("LOGIN"),
  startLoggingIn: createAction("CHANGE LOADING STATE"),
  setLoadingError: createAction("SET LOADING ERROR"),
};

export function loginAction(userName, password) {
  return async (dispatch, getState) => {
    if (userName.length > 0 && password.length > 0) {
      dispatch(actions.startLoggingIn());
      try {
        const { data } = await axios.post(
          `${rootUrl}/login`,
          {
            email: userName,
            password,
          },
          { withCredentials: true }
        );

        await dispatch(
          appActions.setUserData({
            firstName: data.first_name,
            lastName: data.second_name,
            isAdmin: data.is_admin,
          })
        );
      } catch (e) {
        dispatch(
          actions.setLoadingError({
            isError: true,
            message: "Username is not correct",
          })
        );
      }
    } else {
      dispatch(
        actions.setLoadingError({
          isError: true,
          message: "Username and Password should be not empty",
        })
      );
    }
  };
}

export function logoutAction() {
  return async (dispatch, getState) => {
    try {
      const response = await axios.get(`${rootUrl}/logout`, {
        withCredentials: true,
      });
    } catch {}
    dispatch(appActions.logout());
  };
}

export default actions;
