import React, { useState } from "react";
import Typography from "@material-ui/core/Typography";
import TextField from "@material-ui/core/TextField";
import Button from "@material-ui/core/Button";
import { makeStyles } from "@material-ui/core/styles";
import CircularProgress from "@material-ui/core/CircularProgress";
import { red } from "@material-ui/core/colors";
import { useSelector } from "react-redux";
import { rootUrl } from "../../config";
import { useHistory } from "react-router-dom";

import axios from "axios";

export default function EditProfile() {
  const classes = useStyles();

  const { firstName, lastName } = useSelector((state) => state.appReducer);
  const history = useHistory();

  const [isLoading, setIsLoading] = useState(false);
  const [name, setName] = useState(firstName);
  const [surName, setSurName] = useState(lastName);
  const [password, setPassword] = useState("");
  const [isError, setIsError] = useState(false);

  const onSubmit = async () => {
    setIsLoading(true);

    try {
      const response = await axios.put(
        `${rootUrl}/user`,
        {
          firstName: name,
          secondName: surName,
          password: password,
        },
        {
          withCredentials: true,
        }
      );
      history.push("/profile");
    } catch (e) {
      setIsError(true);
    } finally {
      setIsLoading(false);
    }
  };

  return (
    <div style={{ padding: 10 }}>
      <Typography variant="h5"> </Typography>

      <div style={{ display: "flex", flexDirection: "column" }}>
        <TextField
          className={classes.textField}
          label=""
          value={name}
          onChange={(e) => setName(e.target.value)}
          variant="filled"
        />
        <TextField
          className={classes.textField}
          label=""
          variant="filled"
          value={surName}
          onChange={(e) => setSurName(e.target.value)}
        />
        <TextField
          className={classes.textField}
          label=""
          variant="filled"
          type="password"
          value={password}
          onChange={(e) => setPassword(e.target.value)}
        />
      </div>

      {isError && <Typography variant="subheader"></Typography>}

      <div className={classes.submitButtonWrapper}>
        <Button
          type="submit"
          fullWidth
          variant="contained"
          color="primary"
          className={classes.submit}
          disabled={
            isLoading || name === "" || surName === "" || password === ""
          }
          onClick={onSubmit}
        >
          {!isLoading ? "" : <span>&nbsp;&nbsp;</span>}
        </Button>
        {isLoading && (
          <CircularProgress
            size={24}
            className={classes.submitButtonProgress}
          />
        )}
      </div>
    </div>
  );
}

const useStyles = makeStyles((theme) => ({
  submitButtonWrapper: {
    margin: theme.spacing(1),
    position: "relative",
    width: 200,
  },
  submitButtonProgress: {
    position: "absolute",
    top: "50%",
    left: "50%",
    marginTop: -8,
    marginLeft: -12,
  },
  submit: {
    margin: theme.spacing(3, 0, 2),
  },
  textField: {
    width: 200,
    margin: "5px 0px",
  },
}));
