import { handleActions } from "redux-actions";
import companiesActions from "./companiesActions";

const defaultState = {
  companies: [],
  isDataLoaded: false,
  isDataLoading: false,
};

export default handleActions(
  {
    [companiesActions.setCompanies]: (state, action) => {
      return {
        ...state,
        companies: action.payload,
        isDataLoaded: true,
        isDataLoading: false,
      };
    },

    [companiesActions.setLoading]: (state, action) => {
      return {
        ...state,
        isDataLoading: action.payload,
      };
    },
  },
  defaultState
);
