import React, { useState, useEffect } from "react";
import GridLoader from "react-spinners/GridLoader";
import Typography from "@material-ui/core/Typography";
import TextField from "@material-ui/core/TextField";
import Select from "@material-ui/core/Select";
import FormControl from "@material-ui/core/FormControl";
import InputLabel from "@material-ui/core/InputLabel";
import MenuItem from "@material-ui/core/MenuItem";
import Button from "@material-ui/core/Button";
import { makeStyles } from "@material-ui/core/styles";
import CircularProgress from "@material-ui/core/CircularProgress";
import { red } from "@material-ui/core/colors";
import { useSelector, useDispatch } from "react-redux";
import { rootUrl } from "../../config";
import { useHistory } from "react-router-dom";

import { getCompanies } from "../Companies/companiesActions";

import axios from "axios";

export default function AddCompany() {
  const classes = useStyles();
  const [isLoading, setIsLoading] = useState(false);
  const [name, setName] = useState("");
  const [isError, setIsError] = useState(false);
  const [goodResponse, setGoodResponse] = useState("");

  const dispatch = useDispatch();

  const clearFields = () => {
    setName("");
  };

  const onSubmit = async () => {
    setIsLoading(true);

    try {
      await axios.post(
        `${rootUrl}/company`,
        {
          name: name,
        },
        {
          withCredentials: true,
        }
      );
      setGoodResponse(` ${name} `);
      dispatch(getCompanies());
      clearFields();
    } catch (e) {
      setIsError(true);
    } finally {
      setIsLoading(false);
    }
  };

  return (
    <div style={{ padding: 10 }}>
      <Typography variant="h5"> </Typography>

      <div style={{ display: "flex", flexDirection: "column" }}>
        <TextField
          className={classes.textField}
          label=" "
          variant="filled"
          value={name}
          onChange={(e) => setName(e.target.value)}
        />
      </div>

      {isError && <Typography variant="subheader"></Typography>}

      {goodResponse !== "" && (
        <Typography variant="h6" style={{ margin: "10px 0px" }}>
          {goodResponse}
        </Typography>
      )}

      <div className={classes.submitButtonWrapper}>
        <Button
          type="submit"
          fullWidth
          variant="contained"
          color="primary"
          className={classes.submit}
          disabled={isLoading || name === ""}
          onClick={onSubmit}
        >
          {!isLoading ? " " : <span>&nbsp;&nbsp;</span>}
        </Button>
        {isLoading && (
          <CircularProgress
            size={24}
            className={classes.submitButtonProgress}
          />
        )}
      </div>
    </div>
  );
}

const useStyles = makeStyles((theme) => ({
  submitButtonWrapper: {
    margin: theme.spacing(1),
    position: "relative",
    width: 200,
  },
  submitButtonProgress: {
    position: "absolute",
    top: "50%",
    left: "50%",
    marginTop: -8,
    marginLeft: -12,
  },
  submit: {
    margin: theme.spacing(3, 0, 2),
  },
  textField: {
    width: 200,
    margin: "5px 0px",
  },
}));
