/* eslint-disable react-hooks/exhaustive-deps */
import React, { useState, useEffect } from "react";
import Table from "@material-ui/core/Table";
import TableBody from "@material-ui/core/TableBody";
import TableCell from "@material-ui/core/TableCell";
import TableContainer from "@material-ui/core/TableContainer";
import TableHead from "@material-ui/core/TableHead";
import TableRow from "@material-ui/core/TableRow";
import TableFooter from "@material-ui/core/TableFooter";
import TablePagination from "@material-ui/core/TablePagination";
import Paper from "@material-ui/core/Paper";
import TextField from "@material-ui/core/TextField";
import InputAdornment from "@material-ui/core/InputAdornment";
import SearchIcon from "@material-ui/icons/Search";
import FilterList from "@material-ui/icons/FilterList";
import Chip from "@material-ui/core/Chip";
import IconButton from "@material-ui/core/IconButton";
import FirstPageIcon from "@material-ui/icons/FirstPage";
import KeyboardArrowLeft from "@material-ui/icons/KeyboardArrowLeft";
import KeyboardArrowRight from "@material-ui/icons/KeyboardArrowRight";
import LastPageIcon from "@material-ui/icons/LastPage";

import CsvDownload from "react-json-to-csv";

import UpDownIcon from "@material-ui/icons/ImportExport";
import UpIcon from "@material-ui/icons/ArrowUpward";
import DownIcon from "@material-ui/icons/ArrowDownward";

import GridLoader from "react-spinners/GridLoader";
import axios from "axios";
import { Typography } from "@material-ui/core";

import { useLocation, useHistory } from "react-router-dom";

import { rootUrl } from "../../config";

function CellHeader(props) {
  const [showFilter, setShowFilter] = useState(false);
  const [value, setValue] = useState("");

  const width = props.width || 200;

  return (
    <TableCell>
      <div style={{ width }}>
        <div style={styles.rowHeader}>
          {props.sortBy === "up" && (
            <UpIcon
              onClick={() => props.toggleSort()}
              style={{ cursor: "pointer" }}
            />
          )}

          {props.sortBy === "down" && (
            <DownIcon
              onClick={() => props.toggleSort()}
              style={{ cursor: "pointer" }}
            />
          )}

          {!props.sortBy && (
            <UpDownIcon
              onClick={() => props.toggleSort()}
              style={{ cursor: "pointer" }}
            />
          )}
          <span style={{ width: "100%" }}>{props.title}</span>
          <FilterList
            onClick={() => setShowFilter(!showFilter)}
            style={{ cursor: "pointer" }}
          />
        </div>
        {showFilter && (
          <TextField
            helperText="Filter"
            value={value}
            onChange={(e) => setValue(e.target.value)}
            onKeyPress={(e) => e.key === "Enter" && props.addFilter(value)}
          />
        )}
      </div>
    </TableCell>
  );
}

function TablePaginationActions(props) {
  const { count, page, rowsPerPage, onChangePage } = props;

  const handleFirstPageButtonClick = (event) => {
    onChangePage(event, 0);
  };

  const handleBackButtonClick = (event) => {
    onChangePage(event, page - 1);
  };

  const handleNextButtonClick = (event) => {
    onChangePage(event, page + 1);
  };

  const handleLastPageButtonClick = (event) => {
    onChangePage(event, Math.max(0, Math.ceil(count / rowsPerPage) - 1));
  };

  return (
    <div style={{ flexShrink: 0, marginLeft: 10 }}>
      <IconButton
        onClick={handleFirstPageButtonClick}
        disabled={page === 0}
        aria-label="first page"
      >
        <FirstPageIcon />
      </IconButton>
      <IconButton
        onClick={handleBackButtonClick}
        disabled={page === 0}
        aria-label="previous page"
      >
        <KeyboardArrowLeft />
      </IconButton>
      <IconButton
        onClick={handleNextButtonClick}
        disabled={page >= Math.ceil(count / rowsPerPage) - 1}
        aria-label="next page"
      >
        <KeyboardArrowRight />
      </IconButton>
      <IconButton
        onClick={handleLastPageButtonClick}
        disabled={page >= Math.ceil(count / rowsPerPage) - 1}
        aria-label="last page"
      >
        <LastPageIcon />
      </IconButton>
    </div>
  );
}

function useQuery() {
  return new URLSearchParams(useLocation().search);
}

function JsonTable(props) {
  const [initData, setInitData] = useState([]);
  const [data, setData] = useState([]);
  const [keys, setKeys] = useState([]);
  const [isFetched, setIsFetched] = useState(false);
  const [error, setError] = useState(false);
  const [sortBy, setSortBy] = useState({});
  const [search, setSearch] = useState("");
  const [currentPage, setCurrentPage] = useState(0);
  const [rowsPerPage, setRowsPerPage] = useState(25);
  const [tableData, setTableData] = useState([]);
  const [count, setCount] = useState(0);
  const [loading, setLoading] = useState(false);

  const [filters, setFilters] = useState({});

  const query = useQuery();
  const history = useHistory();

  const addFilter = (key, value) => {
    if (value === "") {
      return removeFilter(key);
    }
    const newFilters = { ...filters };
    newFilters[key] = value;
    setFilters(newFilters);
  };

  const removeFilter = (key) => {
    const newFilters = { ...filters };
    delete newFilters[key];
    setFilters(newFilters);
  };

  const handleChangePage = (event, newPage) => {
    setCurrentPage(newPage);
  };

  const handleChangeRowsPerPage = (event) => {
    setRowsPerPage(parseInt(event.target.value, 10));
    setCurrentPage(0);
  };

  const handleChangeSort = (key) => {
    if (sortBy.key !== key) {
      setSortBy({
        key,
        direction: "up",
      });
    } else {
      if (sortBy.direction === "up") {
        setSortBy({
          key,
          direction: "down",
        });
      } else {
        setSortBy({});
      }
    }
  };

  useEffect(() => {
    axios
      .get(props.url, {
        method: "get",
        withCredentials: true,
      })
      .then((req) => {
        if (req.data) {
          if (req.data.length > 0) {
            setKeys(Object.keys(props.keys));
            setData(req.data);
            setInitData(req.data);
            setCount(req.data.length);
          }
        } else {
          setError(true);
        }

        setIsFetched(true);

        const callIdFromQuerry = query.get("callid");

        if (callIdFromQuerry) {
          setFilters({ callid: callIdFromQuerry });
        }
      })
      .catch((e) => {
        setError(true);
        setIsFetched(true);
      });
  }, []);

  useEffect(() => {
    setLoading(true);
    let newJson = [...initData];

    if (search !== "") {
      newJson = initData.filter((data) =>
        Object.values(data).some((e) => {
          if (!e) return false;
          return e.toString().toLowerCase().includes(search.toLowerCase());
        })
      );
    }

    let newData = [...newJson];

    Object.keys(filters).map((key) => {
      newData = newData.filter((data) => data[key].toString() === filters[key]);
    });

    if (sortBy.key) {
      if (sortBy.direction === "up") {
        newData = newData.sort((a, b) =>
          ("" + a[sortBy.key]).localeCompare("" + b[sortBy.key], undefined, {
            numeric: true,
            sensitivity: "base",
          })
        );
      } else {
        newData = newData.sort((a, b) =>
          ("" + b[sortBy.key]).localeCompare("" + a[sortBy.key], undefined, {
            numeric: true,
            sensitivity: "base",
          })
        );
      }
    }

    setData(newData);
    setCount(newData.length);
    setLoading(false);
  }, [filters, search, sortBy]);

  useEffect(() => {
    setTableData(
      data.slice(
        currentPage * rowsPerPage,
        currentPage * rowsPerPage + rowsPerPage
      )
    );
  }, [currentPage, rowsPerPage, data]);

  const renderContent = () => {
    if (!isFetched || loading) {
      return (
        <div
          style={{
            width: "100%",
            height: "100%",
            display: "flex",
            justifyContent: "center",
            alignItems: "center",
          }}
        >
          <GridLoader loading={true} />
        </div>
      );
    }

    if (isFetched && error) {
      return <span>Error</span>;
    }

    return (
      <TableContainer component={Paper}>
        <Table>
          <TableHead>
            <TableRow>
              {keys.map((key) => (
                <CellHeader
                  title={props.keys[key].name.toUpperCase()}
                  addFilter={(val) => addFilter(key, val)}
                  toggleSort={() => handleChangeSort(key)}
                  sortBy={sortBy.key === key ? sortBy.direction : null}
                />
              ))}
            </TableRow>
          </TableHead>
          <TableBody>
            {tableData.map((row) => (
              <TableRow key={row.id}>
                {keys.map((key) => (
                  <TableCell
                    onClick={() =>
                      key === "callid" &&
                      props.nextUrl &&
                      history.push(
                        `tables/?table=${props.nextUrl}&callid=${row[key]}`
                      )
                    }
                    style={{
                      cursor:
                        key === "callid" && props.nextUrl ? "pointer" : "auto",
                    }}
                  >
                    {key === "waverecord" ? (
                      <div
                        style={{
                          minWidth: 300,
                          width: "100%",
                          textAlign: "center",
                        }}
                      >
                        <audio controls preload="none">
                          <source
                            src={`${rootUrl}/waverecord/${row.id}`}
                            type="audio/wav"
                          />
                        </audio>
                      </div>
                    ) : (
                      row[key]
                    )}
                  </TableCell>
                ))}
              </TableRow>
            ))}
          </TableBody>
          <TableFooter>
            <TableRow>
              <TablePagination
                rowsPerPageOptions={[5, 25, 100, 200]}
                colSpan={3}
                count={count}
                rowsPerPage={rowsPerPage}
                page={currentPage}
                onChangePage={handleChangePage}
                onChangeRowsPerPage={handleChangeRowsPerPage}
                ActionsComponent={TablePaginationActions}
              />
            </TableRow>
          </TableFooter>
        </Table>
      </TableContainer>
    );
  };

  return (
    <div style={{ display: "flex", flexDirection: "column" }}>
      <TextField
        id="standard-search"
        label="Search field"
        type="search"
        style={{ width: "200px", margin: "10px 0px 0px 10px" }}
        autocomplete="off"
        InputProps={{
          startAdornment: (
            <InputAdornment position="start">
              <SearchIcon />
            </InputAdornment>
          ),
        }}
        onKeyPress={(e) => e.key === "Enter" && setSearch(e.target.value)}
      />

      <div style={{ margin: "10px 0px 0px 10px", display: "flex" }}>
        {Object.keys(filters).map((key) => (
          <Chip label={`${key}: ${filters[key]}`} style={{ margin: 5 }} />
        ))}
      </div>

      <Typography style={{ margin: "10px 0px 0px 10px" }}>
        {sortBy.key ? (
          `Sort by ${sortBy.key} : ${
            sortBy.direction === "up" ? "desc" : "asc"
          }`
        ) : (
          <span>&nbsp;&nbsp;</span>
        )}
      </Typography>

      <CsvDownload
        data={tableData}
        filename={`${props.url.slice(props.url.lastIndexOf("/") + 1)}.csv`}
        style={{
          borderRadius: "6px",
          border: "1px solid black",
          fontSize: "15px",
          fontWeight: "bold",
          padding: "6px 14px",
          textDecoration: "none",
          cursor: "pointer",
          width: "150px",
          margin: 10,
        }}
      >
        Export to CSV
      </CsvDownload>

      {renderContent()}
    </div>
  );
}

const styles = {
  rowHeader: {
    display: "flex",
    justifyContent: "space-between",
    alignItems: "center",
  },
};

export default JsonTable;
