import { handleActions } from "redux-actions";
import loginActions from "./loginActions";

const defaultState = {
  isLoadingLogging: false,
  errorWhenLogging: false,
  errorMessage: "",
};

export default handleActions(
  {
    [loginActions.startLoggingIn]: (state) => {
      return {
        ...state,
        isLoadingLogging: true,
        errorWhenLogging: false,
        errorMessage: "",
      };
    },

    [loginActions.setLoadingError]: (state, action) => {
      const { isError, message } = action.payload;
      return {
        ...state,
        isLoadingLogging: false,
        errorWhenLogging: isError,
        errorMessage: message || "",
      };
    },
  },
  defaultState
);
