import { handleActions } from "redux-actions";
import profileActions from "./profileActions";

const defaultState = {
  isDataLoaded: false,
  isDataLoading: false,
  isError: false,
  userData: {},
  companyData: {},
};

export default handleActions(
  {
    [profileActions.startLoading]: (state) => {
      return {
        ...state,
        userData: {},
        isDataLoaded: false,
        isDataLoading: true,
      };
    },

    [profileActions.setProfileData]: (state, action) => {
      return {
        ...state,
        userData: action.payload.user,
        companyData: action.payload.companies,
        isError: false,
        isDataLoaded: true,
        isDataLoading: false,
      };
    },
  },
  defaultState
);
