import { createAction } from "redux-actions";
import axios from "axios";
import appActions from "../../app/appActions";

import { rootUrl } from "../../config";

const actions = {
  startLoading: createAction("START_LOADING_PROFILE"),
  setProfileData: createAction("SET_PROFILE_DATA"),
};

export function startLoading(userName, password) {
  return async (dispatch, getState) => {
    dispatch(actions.startLoading());

    const response = await axios.get(`${rootUrl}/user`, {
      withCredentials: true,
    });

    dispatch(actions.setProfileData(response.data));
  };
}

export default actions;
