import { createAction } from "redux-actions";
import axios from "axios";
import { rootUrl } from "../../config";

import { tableNameEnum } from "./tablesUtil";

const actions = {
  setInitData: createAction("Set_init_Data"),
  setLoading: createAction("setLoading"),
};

export function fetchInitData(tableName) {
  return async (dispatch, getState) => {
    dispatch(actions.setLoading(true));

    const response = await axios.get(`${rootUrl}/${tableName}`, {
      withCredentials: true,
    });

    dispatch(actions.setInitData({tableName, response: response || []}))
  };
}

export default actions;
