import { createAction } from "redux-actions";
import axios from "axios";

import { rootUrl } from "../config";

const actions = {
  setUserData: createAction("LOGIN"),
  authenticateUser: createAction("AUTHENTICATE_USER"),
  logout: createAction("USER_LOGOUT"),
  initAuth: createAction("INIT_AUTH"),
};

export function authenticate() {
  return async (dispatch, getState) => {
    try {
      const authResponse = await axios.get(`${rootUrl}/auth`, {
        withCredentials: true,
      });

      if (authResponse.status === 200) {
        const { data } = authResponse;
        dispatch(
          actions.setUserData({
            firstName: data.first_name,
            lastName: data.second_name,
            isAdmin: data.is_admin,
          })
        );
      }
    } catch (e) {
    } finally {
      dispatch(actions.initAuth());
    }
  };
}

export default actions;
