import React, { useState, useEffect } from "react";
import { useSelector, useDispatch } from "react-redux";
import { loginAction } from "./loginActions";

import Avatar from "@material-ui/core/Avatar";
import Button from "@material-ui/core/Button";
import CssBaseline from "@material-ui/core/CssBaseline";
import TextField from "@material-ui/core/TextField";
import Link from "@material-ui/core/Link";
import Paper from "@material-ui/core/Paper";
import Box from "@material-ui/core/Box";
import Grid from "@material-ui/core/Grid";
import LockOutlinedIcon from "@material-ui/icons/LockOutlined";
import Typography from "@material-ui/core/Typography";
import { makeStyles } from "@material-ui/core/styles";
import CircularProgress from "@material-ui/core/CircularProgress";
import { red } from "@material-ui/core/colors";

import { useHistory } from "react-router-dom";

function Copyright() {
  return (
    <Typography variant="body2" color="textSecondary" align="center">
      {"Copyright  "}
      <Link color="inherit" href="https://material-ui.com/">
        ForteCom
      </Link>{" "}
      {new Date().getFullYear()}
      {"."}
    </Typography>
  );
}

const useStyles = makeStyles((theme) => ({
  root: {
    height: "100vh",
  },
  image: {
    backgroundImage:
      "url(https://cutewallpaper.org/21/wallpapers-with-numbers/Mathematics-HD-Desktop-Wallpapers-PixelsTalk.Net.jpg)",
    backgroundRepeat: "no-repeat",
    backgroundColor:
      theme.palette.type === "light"
        ? theme.palette.grey[50]
        : theme.palette.grey[900],
    backgroundSize: "cover",
    backgroundPosition: "center",
  },
  paper: {
    margin: theme.spacing(8, 4),
    display: "flex",
    flexDirection: "column",
    alignItems: "center",
  },
  avatar: {
    margin: theme.spacing(1),
    backgroundColor: theme.palette.secondary.main,
  },
  form: {
    width: "100%", // Fix IE 11 issue.
    marginTop: theme.spacing(1),
  },
  submitButtonWrapper: {
    margin: theme.spacing(1),
    position: "relative",
  },
  submitButtonProgress: {
    position: "absolute",
    top: "50%",
    left: "50%",
    marginTop: -8,
    marginLeft: -12,
  },
  submit: {
    margin: theme.spacing(3, 0, 2),
  },
  errorText: {
    color: red[500],
  },
}));

export default function SignInSide() {
  const classes = useStyles();

  const dispatch = useDispatch();
  const { isLoadingLogging, errorWhenLogging, errorMessage } = useSelector(
    (state) => state.loginReducer
  );

  const { isLoggedIn } = useSelector((state) => state.appReducer);
  const history = useHistory();

  useEffect(() => {
    if (isLoggedIn) {
      history.push("/");
    }
  }, [isLoggedIn]);

  const [userName, setUserName] = useState("");
  const [password, setpassword] = useState("");

  const onSubmit = (e) => {
    e.preventDefault();
    dispatch(loginAction(userName, password));
  };

  return (
    <Grid container component="main" className={classes.root}>
      <CssBaseline />
      <Grid item xs={false} sm={4} md={7} className={classes.image} />
      <Grid item xs={12} sm={8} md={5} component={Paper} elevation={6} square>
        <div className={classes.paper}>
          <Avatar className={classes.avatar}>
            <LockOutlinedIcon />
          </Avatar>
          <Typography component="h1" variant="h5">
            Sign in
          </Typography>
          <form className={classes.form} noValidate onSubmit={onSubmit}>
            <TextField
              value={userName}
              onChange={(e) => setUserName(e.target.value)}
              variant="outlined"
              margin="normal"
              fullWidth
              label="User Name"
              autoFocus
            />
            <TextField
              value={password}
              onChange={(e) => setpassword(e.target.value)}
              variant="outlined"
              margin="normal"
              fullWidth
              name="password"
              label="Password"
              type="password"
              id="password"
            />

            {errorWhenLogging && (
              <Grid container>
                <Grid item xs>
                  <Typography
                    component="h1"
                    variant="h5"
                    className={classes.errorText}
                  >
                    {errorMessage}
                  </Typography>
                </Grid>
              </Grid>
            )}

            <div className={classes.submitButtonWrapper}>
              <Button
                type="submit"
                fullWidth
                variant="contained"
                color="primary"
                className={classes.submit}
                disabled={isLoadingLogging}
              >
                {!isLoadingLogging ? "Sign in" : <span>&nbsp;&nbsp;</span>}
              </Button>
              {isLoadingLogging && (
                <CircularProgress
                  size={24}
                  className={classes.submitButtonProgress}
                />
              )}
            </div>

            {/* <Grid container>
              <Grid item xs>
                <Link href="#" variant="body2">
                  Forgot password?
                </Link>
              </Grid>
              <Grid item>
                <Link href="#" variant="body2">
                  {"Don't have an account? Sign Up"}
                </Link>
              </Grid>
            </Grid> */}
            <Box mt={5}>
              <Copyright />
            </Box>
          </form>
        </div>
      </Grid>
    </Grid>
  );
}
