import React, { useState } from "react";
import { makeStyles } from "@material-ui/core/styles";
import Card from "@material-ui/core/Card";
import CardActionArea from "@material-ui/core/CardActionArea";
import CardActions from "@material-ui/core/CardActions";
import CardContent from "@material-ui/core/CardContent";
import Badge from "@material-ui/core/Badge";
import IconButton from "@material-ui/core/IconButton";
import CircularProgress from "@material-ui/core/CircularProgress";
import Button from "@material-ui/core/Button";
import Typography from "@material-ui/core/Typography";
import Backdrop from "@material-ui/core/Backdrop";

import ExpandMoreIcon from "@material-ui/icons/ExpandMore";
import ExpandLessIcon from "@material-ui/icons/ExpandLess";

import Table from "@material-ui/core/Table";
import TableBody from "@material-ui/core/TableBody";
import TableCell from "@material-ui/core/TableCell";
import TableContainer from "@material-ui/core/TableContainer";
import TableHead from "@material-ui/core/TableHead";
import TableRow from "@material-ui/core/TableRow";
import Paper from "@material-ui/core/Paper";
import HighlightOffIcon from "@material-ui/icons/HighlightOff";
import axios from "axios";
import { rootUrl } from "../../config";
import { useDispatch } from "react-redux";
import { getCompanies } from "./companiesActions";

import PersonIcon from "@material-ui/icons/Person";
import {
  Divider,
  FormControl,
  InputLabel,
  Select,
  MenuItem,
} from "@material-ui/core";

const useStyles = makeStyles({
  root: {
    minWidth: 200,
    margin: "10px 0px 10px 10px",
  },
  media: {
    height: 140,
  },
});

export default function ProjectComponent({
  projectName = "",
  projectId = 1,
  searchText = "",
  isSearching = false,
  userCount = 0,
  allUsers = [],
  currentUsers = [],
}) {
  const [focused, setFocused] = useState(false);
  const [selectedUser, setSelectedUser] = useState(-1);

  const [loading, setLoading] = useState(false);

  const dispatch = useDispatch();

  const classes = useStyles();
  const backgroundColor =
    projectName &&
    isSearching &&
    projectName.toLowerCase().includes(searchText.toLowerCase())
      ? "wheat"
      : "white";

  const usersToAdd = allUsers.filter((u) =>
    currentUsers.every((cu) => cu.id !== u.id)
  );

  return (
    <Card
      className={classes.root}
      style={{ backgroundColor, width: '100%' }}
      disabled={true}
    >
      <CardContent>
        <div
          style={{
            width: "100%",
            display: "flex",
            justifyContent: "space-between",
            alignItems: "center",
          }}
        >
          <Typography gutterBottom variant="h5" component="h2">
            {projectName}
          </Typography>

          {userCount > 0 && (
            <Badge
              badgeContent={userCount}
              style={{ paddingBottom: 8, marginLeft: 10 }}
            >
              <PersonIcon />
            </Badge>
          )}
        </div>

        <Divider />

        <Button style={{ width: "100%" }} onClick={() => setFocused(!focused)}>
          {focused ? <ExpandLessIcon /> : <ExpandMoreIcon />}
        </Button>

        {focused && (
          <>
            {usersToAdd.length > 0 && (
              <div style={{ display: "flex", margin: "20px 0px" }}>
                <FormControl style={{ width: 200 }} variant="filled">
                  <InputLabel></InputLabel>
                  <Select
                    value={selectedUser}
                    onChange={(e) => setSelectedUser(e.target.value)}
                    style={{ height: 50 }}
                  >
                    {usersToAdd.map((c) => (
                      <MenuItem value={c.id}>
                        {c.firstName || ""} {c.secondName || ""} (
                        {c.login || ""})
                      </MenuItem>
                    ))}
                  </Select>
                </FormControl>

                <Button
                  variant="contained"
                  style={{
                    margin: "10px 0px 10px 5px",
                    backgroundColor: "#6ccf70",
                  }}
                  disabled={!selectedUser || selectedUser === -1}
                  onClick={async () => {
                    const user = allUsers.find((u) => u.id === selectedUser);
                    try {
                      setLoading(true);
                      await axios.post(
                        `${rootUrl}/projects/${selectedUser}`,
                        {
                          project_id: projectId,
                          project_name: projectName,
                          user_id: selectedUser,
                          user_full_name: `${user.firstName} ${user.secondName} (${user.login})`,
                        },
                        { withCredentials: true }
                      );
                      dispatch(getCompanies());
                    } finally {
                      setLoading(false);
                    }
                  }}
                >
                  {loading ? "" : " "}
                </Button>
              </div>
            )}

            <TableContainer component={Paper}>
              <Table>
                <TableHead>
                  <TableRow>
                    <TableCell></TableCell>
                    <TableCell></TableCell>
                    <TableCell>Email</TableCell>
                    <TableCell></TableCell>
                    <TableCell align="right">
                         
                    </TableCell>
                  </TableRow>
                </TableHead>
                <TableBody>
                  {currentUsers.length === 0 && (
                    <Typography variant="h5" style={{ margin: 20 }}>
                       
                    </Typography>
                  )}

                  {currentUsers.map((row) => (
                    <TableRow key={row.login}>
                      <TableCell component="th">
                        {row.firstName || "-"}
                      </TableCell>
                      <TableCell>{row.secondName || "-"}</TableCell>
                      <TableCell>{row.email || "-"}</TableCell>
                      <TableCell>{row.login || "-"}</TableCell>
                      <TableCell align="right">
                        <Button variant="contained">
                          <HighlightOffIcon />
                        </Button>
                      </TableCell>
                    </TableRow>
                  ))}
                </TableBody>
              </Table>
            </TableContainer>
          </>
        )}
      </CardContent>
    </Card>
  );
}
