import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import { useSelector, useDispatch } from "react-redux";
import AppBar from "@material-ui/core/AppBar";
import Toolbar from "@material-ui/core/Toolbar";
import Typography from "@material-ui/core/Typography";
import IconButton from "@material-ui/core/IconButton";
import BeachAccess from "@material-ui/icons/BeachAccess";
import SettingsIcon from '@material-ui/icons/Settings';
import MenuItem from "@material-ui/core/MenuItem";
import Menu from "@material-ui/core/Menu";
import Divider from "@material-ui/core/Divider";


import { useHistory } from "react-router-dom";

import { logoutAction } from "../scenes/Login/loginActions";

const useStyles = makeStyles((theme) => ({
  appBar: {
    zIndex: theme.zIndex.drawer + 1,
  },
  companyLogo: {
    marginRight: theme.spacing(2),
  },
  userNameTypography: {
    marginRight: theme.spacing(1),
  },
  title: {
    flexGrow: 1,
  },
}));

export default function TopMenu() {
  const classes = useStyles();
  const [anchorEl, setAnchorEl] = React.useState(null);
  const open = Boolean(anchorEl);
  const dispatch = useDispatch();

  const { firstName, lastName, isAdmin } = useSelector(
    (state) => state.appReducer
  );

  const history = useHistory();

  const handleMenu = (event) => {
    setAnchorEl(event.currentTarget);
  };

  const handleClose = () => {
    setAnchorEl(null);
  };

  return (
    <AppBar position="fixed" className={classes.appBar}>
      <Toolbar>
        <BeachAccess className={classes.companyLogo} />
        <Typography variant="h5" className={classes.title}>
          FORTCOM
        </Typography>

        <div style={{ display: "flex", alignItems: "center" }}>
          {isAdmin && (
            <Typography
              variant="subtitle2"
              className={classes.userNameTypography}
            >
              
            </Typography>
          )}

          <Typography
            variant="subtitle2"
            className={classes.userNameTypography}
          >
            {firstName} {lastName}
          </Typography>
          <IconButton
            aria-label="account of current user"
            aria-controls="menu-appbar"
            aria-haspopup="true"
            onClick={handleMenu}
            color="inherit"
          >
            <SettingsIcon />
          </IconButton>
          <Menu
            id="menu-appbar"
            anchorEl={anchorEl}
            open={open}
            onClose={handleClose}
          >
            <MenuItem
              onClick={() => {
                handleClose();
                history.push("/profile");
              }}
            >
              
            </MenuItem>

            <Divider />

            <MenuItem
              onClick={() => {
                handleClose();
                history.push("/profile/edit");
              }}
            >
              
            </MenuItem>

            <Divider />

            <MenuItem onClick={() => dispatch(logoutAction())}></MenuItem>
          </Menu>
        </div>
      </Toolbar>
    </AppBar>
  );
}
