import React from "react";
import Drawer from "@material-ui/core/Drawer";
import List from "@material-ui/core/List";
import Divider from "@material-ui/core/Divider";
import ListItem from "@material-ui/core/ListItem";
import ListItemIcon from "@material-ui/core/ListItemIcon";
import ListItemText from "@material-ui/core/ListItemText";
import BusinessIcon from "@material-ui/icons/Business";
import TableChartIcon from "@material-ui/icons/TableChart";
import PersonAddIcon from "@material-ui/icons/PersonAdd";
import ExitToAppIcon from "@material-ui/icons/ExitToApp";
import AddCompanyIcon from "@material-ui/icons/GroupAdd";
import AddProjectIcon from '@material-ui/icons/PostAdd';
import { useHistory } from "react-router-dom";
import { useSelector, useDispatch } from "react-redux";
import { logoutAction } from "../scenes/Login/loginActions";

export default function LeftMenu() {
  const history = useHistory();

  const { isAdmin } = useSelector((state) => state.appReducer);
  const dispatch = useDispatch();

  return (
    <Drawer variant="permanent" style={{ top: "auto" }}>
      <div
        style={{
          width: "220px",
          marginTop: "64px",
          display: "flex",
          flexDirection: "column",
          justifyContent: "flex-start",
          height: "100%",
        }}
      >
        <List>
          <ListItem
            button
            key={""}
            onClick={() => history.push("/tables")}
          >
            <ListItemIcon>
              <TableChartIcon />
            </ListItemIcon>
            <ListItemText primary={""} />
          </ListItem>
          <ListItem
            button
            key={"  "}
            onClick={() => history.push("/companieslist")}
          >
            <ListItemIcon>
              <BusinessIcon />
            </ListItemIcon>
            <ListItemText primary={"  "} />
          </ListItem>
        </List>

        {isAdmin && (
          <>
            <Divider />
            <List>
              <ListItem
                button
                key={""}
                onClick={() => history.push("/user/add")}
              >
                <ListItemIcon>
                  <PersonAddIcon />
                </ListItemIcon>
                <ListItemText primary={" "} />
              </ListItem>
            </List>
            <List>
              <ListItem
                button
                key={""}
                onClick={() => history.push("/company/add")}
              >
                <ListItemIcon>
                  <AddCompanyIcon />
                </ListItemIcon>
                <ListItemText primary={" "} />
              </ListItem>
            </List>
            <List>
              <ListItem
                button
                key={""}
                onClick={() => history.push("/project/add")}
              >
                <ListItemIcon>
                  <AddProjectIcon />
                </ListItemIcon>
                <ListItemText primary={" "} />
              </ListItem>
            </List>
          </>
        )}

        <Divider style={{ marginTop: "auto" }} />
        <List>
          <ListItem
            button
            key={""}
            onClick={() => dispatch(logoutAction())}
          >
            <ListItemIcon>
              <ExitToAppIcon />
            </ListItemIcon>
            <ListItemText primary={""} />
          </ListItem>
        </List>
      </div>
    </Drawer>
  );
}
