import React, { useEffect, useState } from "react";
import GridLoader from "react-spinners/GridLoader";
import { useSelector, useDispatch } from "react-redux";
import Divider from "@material-ui/core/Divider";
import ProjectComponent from "./ProjectComponent";
import AddProjectCard from "./AddProjectCard";
import Typography from "@material-ui/core/Typography";
import InputAdornment from "@material-ui/core/InputAdornment";
import TextField from "@material-ui/core/TextField";
import Button from "@material-ui/core/Button";
import SearchIcon from "@material-ui/icons/Search";

import { getCompanies } from "./companiesActions";

const useGetCompanies = () => {
  const { isDataLoaded } = useSelector((state) => state.companiesReducer);
  const dispatch = useDispatch();
  useEffect(() => {
    !isDataLoaded && dispatch(getCompanies());
  }, []);
};

export default function Companies() {
  const { isDataLoaded, isDataLoading, companies } = useSelector(
    (state) => state.companiesReducer
  );

  const [searchText, setSearchText] = useState("");
  const [isSearching, setIsSearching] = useState(false);

  useGetCompanies();

  const [currentCompanies, setCurrentCompanies] = useState(companies);

  useEffect(() => {
    setCurrentCompanies(companies);
  }, [companies]);

  console.log(currentCompanies);

  console.log(companies);

  const setSearch = (_inputText) => {
    const inputText = _inputText.toLowerCase();

    if (_inputText === "") {
      setIsSearching(0);
      setCurrentCompanies(companies);
    } else {
      const newCompanies = companies.filter((comp) => {
        if (comp.name.toLowerCase().includes(inputText)) return true;

        if (comp.apps) {
          return comp.apps.some((a) =>
            a.project_name.toLowerCase().includes(inputText)
          );
        }

        return false;
      });

      setCurrentCompanies(newCompanies);
      setSearchText(inputText);
      setIsSearching(true);
    }
  };

  if (isDataLoading) {
    return (
      <div
        style={{
          width: "100%",
          height: "80vh",
          display: "flex",
          justifyContent: "center",
          alignItems: "center",
        }}
      >
        <GridLoader loading={true} size={50} color={"rgb(65,83,175)"} />
      </div>
    );
  }

  if (!isDataLoaded) {
    return null;
  }

  return (
    <div style={{ padding: 10 }}>
      <div style={{ display: "flex", alignItems: "flex-end" }}>
        <TextField
          id="standard-search"
          label="  "
          type="search"
          style={{ margin: 10, width: "300px" }}
          onReset={() => console.log("res")}
          onChange={(e) => setSearch(e.target.value)}
          InputProps={{
            startAdornment: (
              <InputAdornment position="start">
                <SearchIcon />
              </InputAdornment>
            ),
          }}
        />
      </div>

      {currentCompanies.length > 0 ? (
        currentCompanies.map((company, i) => {
          const isCompSearched =
            isSearching &&
            isSearching &&
            company.name.toLowerCase().includes(searchText);

          return (
            <div
              style={{
                display: "flex",
                flexDirection: "column",
                width: "100%",
                marginBottom: 20,
              }}
            >
              <Typography
                variant="h5"
                style={{
                  backgroundColor: isCompSearched ? "wheat" : "white",
                }}
              >
                {company.name}
              </Typography>
              <Typography variant="subtitle1">: </Typography>
              <div style={{ display: "flex", flexWrap: "wrap" }}>
                {company.apps ? (
                  company.apps.map((app) => (
                    <ProjectComponent
                      projectName={app.project_name}
                      projectId={app.id}
                      userCount={app.AppUsers ? app.AppUsers.length : 0}
                      searchText={searchText}
                      isSearching={isSearching}
                      currentUsers={app.AppUsers || []}
                      allUsers={company.users || []}
                    />
                  ))
                ) : (
                  <AddProjectCard />
                )}
              </div>
              <Divider style={{ marginTop: 20 }} />
            </div>
          );
        })
      ) : (
        <Typography>     </Typography>
      )}
    </div>
  );
}
