import React, { useState, useEffect } from "react";
import JsonTable from "./JsonTable";
import Button from "@material-ui/core/Button";
import styled from "styled-components";
import Typography from "@material-ui/core/Typography";
import { useDispatch } from "react-redux";
import { fetchInitData } from "./tablesActions";
import { rootUrl } from "../../config";
import { tableNameEnum } from "./tablesUtil";
import { useLocation, useHistory } from "react-router-dom";

import "typeface-roboto";

function useQuery() {
  return new URLSearchParams(useLocation().search);
}

const tableEnum = {
  callsout: 1,
  calls: 2,
  vars: 3,
  ars: 4,
};

export default function TablesComponent() {
  const query = useQuery();

  const tableFromQuery = tableEnum[query.get("table")];

  console.log(tableFromQuery);

  const [selected, setSelected] = useState(tableFromQuery || 2);

  const dispatch = useDispatch();
  const history = useHistory();

  useEffect(() => {
    setSelected(tableFromQuery || 1);
  }, [tableFromQuery]);

  return (
    <div style={{ padding: 10 }}>
      <Typography variant="h6"></Typography>
      <div style={{ display: "flex", marginTop: 10 }}>
        <MenuButton
          variant="contained"
          onClick={() => {
            history.push("/tables?table=callsout");
          }}
          color={selected === 1 ? "primary" : "auto"}
        >
           
        </MenuButton>
        <MenuButton
          variant="contained"
          onClick={() => {
            history.push("/tables?table=calls");
          }}
          color={selected === 2 ? "primary" : "auto"}
        >
           
        </MenuButton>

        <MenuButton
          variant="contained"
          onClick={() => {
            history.push("/tables?table=vars");
          }}
          color={selected === 3 ? "primary" : "auto"}
        >
           
        </MenuButton>
        <MenuButton
          variant="contained"
          onClick={() => {
            history.push("/tables?table=ars");
          }}
          color={selected === 4 ? "primary" : "auto"}
        >
           
        </MenuButton>
      </div>

      {selected === 1 && (
        <JsonTable
          url={`${rootUrl}/callsout`}
          tableName={tableNameEnum.callsout}
          nextUrl={`calls`}
          keys={callsOutKeys}
        />
      )}
      {selected === 2 && (
        <JsonTable
          url={`${rootUrl}/calls`}
          tableName={tableNameEnum.calls}
          nextUrl={`vars`}
          keys={callsKeys}
        />
      )}
      {selected === 3 && (
        <JsonTable
          url={`${rootUrl}/vars`}
          tableName={tableNameEnum.ars}
          nextUrl={`ars`}
          keys={varsKeys}
        />
      )}

      {selected === 4 && (
        <JsonTable
          url={`${rootUrl}/ars`}
          tableName={tableNameEnum.vars}
          keys={arsKeys}
        />
      )}
    </div>
  );
}

const MenuButton = styled(Button)`
  && {
    margin-right: 10px;
  }
`;

