import React, { useEffect } from "react";
import Typography from "@material-ui/core/Typography";
import List from "@material-ui/core/List";
import ListItem from "@material-ui/core/ListItem";
import Person from "@material-ui/icons/Person";
import { makeStyles } from "@material-ui/core/styles";
import Divider from "@material-ui/core/Divider";
import { useSelector, useDispatch } from "react-redux";
import GridLoader from "react-spinners/GridLoader";

import CompanyCard from "./CompanyCard";
import ProjectCard from "./ProjectCard";
import { startLoading } from "./profileActions";

const useGetData = () => {
  const { isDataLoaded } = useSelector((state) => state.profileReducer);
  const dispatch = useDispatch();
  useEffect(() => {
    !isDataLoaded && dispatch(startLoading());
  }, []);
};

export default function Profile() {
  useGetData();

  const classes = useStyles();

  const { isDataLoaded, isDataLoading, userData, companyData } = useSelector(
    (state) => state.profileReducer
  );

  console.log(userData);
  console.log(companyData);

  if (isDataLoading) {
    return (
      <div
        style={{
          width: "100%",
          height: "80vh",
          display: "flex",
          justifyContent: "center",
          alignItems: "center",
        }}
      >
        <GridLoader loading={true} size={50} color={"rgb(65,83,175)"} />
      </div>
    );
  }

  if (!isDataLoaded) {
    return null;
  }

  return (
    <div style={{ padding: 10 }}>
      <div
        style={{
          marginTop: 10,
          display: "flex",
          flexDirection: "column",
        }}
      >
        <Person style={{ fontSize: 160 }} />
        <Typography variant="h5">   </Typography>
        <List component="nav" className={classes.list}>
          <ListItem>
            <Typography>: </Typography>
            <Typography style={{ fontWeight: "bold", marginLeft: 5 }}>
              {userData.login}
            </Typography>
          </ListItem>
          <Divider />
          <ListItem>
            <Typography>email: </Typography>
            <Typography style={{ fontWeight: "bold", marginLeft: 5 }}>
              {userData.email}
            </Typography>
          </ListItem>
          <Divider />
          <ListItem>
            <Typography>: </Typography>
            <Typography style={{ fontWeight: "bold", marginLeft: 5 }}>
              {userData.firstName}
            </Typography>
          </ListItem>
          <Divider />
          <ListItem>
            <Typography>: </Typography>
            <Typography style={{ fontWeight: "bold", marginLeft: 5 }}>
              {userData.secondName}
            </Typography>
          </ListItem>
          {userData.isAdmin && (
            <>
              <Divider />
              <ListItem>
                <Typography>: </Typography>
                <Typography style={{ fontWeight: "bold", marginLeft: 5 }}>
                  
                </Typography>
              </ListItem>
            </>
          )}
        </List>

        <Typography variant="h5"></Typography>
        <div style={{ display: "flex", flexWrap: "wrap" }}>
          <CompanyCard
            companyName={userData.companyName}
            companyDescription={`  ${userData.companyName}`}
          />
        </div>

        <Typography variant="h5" style={{ marginTop: 30 }}>
           
        </Typography>
        <div style={{ display: "flex", flexWrap: "wrap" }}>
          {userData.appNames.map((appName) => (
            <ProjectCard
              companyName={userData.companyName}
              projectName={appName}
              key={appName}
            />
          ))}
        </div>
      </div>
    </div>
  );
}

const useStyles = makeStyles((theme) => ({
  list: {
    width: "100%",
    maxWidth: 560,
    backgroundColor: theme.palette.background.paper,
    border: `1px solid ${theme.palette.divider}`,
    marginTop: 10,
    marginBottom: 30,
  },
  nested: {
    paddingLeft: theme.spacing(4),
  },
}));
