import React, { useEffect } from "react";
import { useSelector, useDispatch } from "react-redux";
import {
  BrowserRouter as Router,
  Switch,
  Route,
  Redirect,
} from "react-router-dom";
import Login from "../scenes/Login";
import Tables from "../scenes/Tables";
import Companies from "../scenes/Companies";
import Profile from "../scenes/Profile";
import EditProfile from "../scenes/EditProfile";
import AddUser from "../scenes/AddUser";
import AddCompany from "../scenes/AddCompany";
import AddProject from "../scenes/AddProject";
import LeftMenu from "../components/LeftMenu";
import TopMenu from "../components/TopMenu";
import { authenticate } from "./appActions";

const useAuth = () => {
  const dispatch = useDispatch();
  useEffect(() => {
    dispatch(authenticate());
  }, []);
};

export default function App() {
  const { isLoggedIn, initAuth } = useSelector((state) => state.appReducer);

  useAuth();

  if (!initAuth) {
    return null;
  }

  return (
    <Router>
      <div>
        {isLoggedIn && (
          <>
            <LeftMenu />
            <TopMenu />
          </>
        )}
        <div
          style={isLoggedIn ? { marginLeft: "220px", marginTop: "64px" } : {}}
        >
          <Switch>
            <Route exact path="/login">
              <Login />
            </Route>
            <ProtectedRoute exact path="/">
              <Redirect
                to={{
                  pathname: "/tables",
                }}
              />
            </ProtectedRoute>
            <ProtectedRoute exact path="/tables">
              <Tables />
            </ProtectedRoute>
            <ProtectedRoute exact path="/companieslist">
              <Companies />
            </ProtectedRoute>
            <ProtectedRoute exact path="/profile">
              <Profile />
            </ProtectedRoute>
            <ProtectedRoute exact path="/profile/edit">
              <EditProfile />
            </ProtectedRoute>
            <ProtectedRoute exact path="/user/add">
              <AddUser />
            </ProtectedRoute>
            <ProtectedRoute exact path="/company/add">
              <AddCompany />
            </ProtectedRoute>
            <ProtectedRoute exact path="/project/add">
              <AddProject />
            </ProtectedRoute>
            <ProtectedRoute path="*">
              <span>no such page</span>
            </ProtectedRoute>
          </Switch>
        </div>
      </div>
    </Router>
  );
}

const ProtectedRoute = ({ children, ...rest }) => {
  const { isLoggedIn } = useSelector((state) => state.appReducer);
  return (
    <Route
      {...rest}
      render={({ location }) =>
        isLoggedIn ? (
          children
        ) : (
          <Redirect
            to={{
              pathname: "/login",
            }}
          />
        )
      }
    />
  );
};
