import { handleActions } from "redux-actions";
import appActions from "./appActions";

const defaultState = {
  isLoggedIn: false,
  firstName: "",
  lastName: "",
  isAdmin: false,
  initAuth: false,
};

export default handleActions(
  {
    [appActions.initAuth]: (state) => {
      return {
        ...state,
        initAuth: true,
      };
    },
    [appActions.logout]: () => {
      return {
        ...defaultState,
        initAuth: true,
      };
    },
    [appActions.setUserData]: (state, action) => {
      return {
        ...state,
        ...action.payload,
        isLoggedIn: true,
      };
    },
  },
  defaultState
);
